.square {
    width: 60px;
    height: 60px;
    font-size: 24px;
    text-align: center;
    line-height: 60px;
    border: 1px solid #ddd;
    background-color: #fff;
  }
  
  .square.highlight {
    background-color: yellow; /* Highlight the winning square */
    border: 2px solid gold;
  }
  